@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;900&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #24283b;
  color: #e3e3e3;
}

.header-container {
  margin: 0 auto;
  max-width: 640px;
  min-width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

nav.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #424972;
  padding: 8px;
}

.navbar .logo {
  height: 3rem;
  display: flex;
  align-items: center;
  color: #24283b;
}

.navbar .logo img {
  height: 100%;
}

.btn-google {
  height: 2.5rem;
  background-color: white;
  border: 1px solid whitesmoke;
  border-radius: 3px;
  display: flex;
  align-items: center;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
  color: #353535;
  cursor: pointer;
  transition: box-shadow 218ms, visibility 180ms, opacity 180ms;
  margin: 0 5px;
}

.btn-google:hover {
  box-shadow: 0 0 3px 3px rgb(255 255 255 / 30%);
}

.btn-google:active {
  box-shadow: 0 0 3px 3px rgb(255 255 255 / 30%);
  background-color: rgba(255 255 255 / 80%);
}

.btn-google:disabled:hover {
  cursor: auto;
  cursor: initial;
  box-shadow: none;
  box-shadow: initial;
}

.btn-google:disabled {
  color: #35353555;
}

.btn-google:disabled::before {
  opacity: 0.8;
}

.btn-google::before {
  content: url(/static/media/ic_google.18d37e9f.svg);
  margin-right: 10px;
  transform: translateY(2px);
  width: 25px;
}

.logged-in-view {
  display: flex;
  align-items: center;
  height: 3rem;
}

.logged-in-view:hover {
  /* text-decoration: underline; */
  /* cursor: pointer; */
}

.logged-in-view:hover .btn-logout {
  visibility: visible;
  opacity: 1;
}

.logged-in-view .user-avatar {
  height: 100%;
  padding: 5px;
}

.user-avatar img {
  border-radius: 50%;
  height: 100%;
}

.btn-logout {
  visibility: hidden;
  opacity: 0;
}

main {
  max-width: 640px;
  min-width: 300px;
  margin: 0 auto;
}

.todo-form {
  background: #313654;
  border-radius: 8px;
  padding: 30px;
}

.todo-form input {
  font-size: 1.5rem;
  width: 100%;
  display: block;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  margin: 10px 0;
  font-family: Oswald, sans-serif;
}

.todo-form button {
  border: none;
  width: 100%;
  padding: 10px 20px;
  margin: 30px 0;
  border-radius: 8px;
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  background: #3178c6;
  color: white;
  outline: none;
}

.todo-form button:hover {
  cursor: pointer;
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}

.todo-list-wrapper {
  margin: 2rem auto;
}

.todos-loader {
  display: flex;
  justify-content: center;
}

.todo-list {
  list-style: none;
  padding: 0;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin: 0.5rem auto;
  background-color: #313654;
  border-radius: 8px;
}
.todo-item .todo-content {
  display: flex;
  flex-direction: column;
}

.todo-tags {
  display: flex;
  flex-wrap: wrap;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.todo-item .todo-text {
  font-size: 1.5rem;
}

.todo-tags div {
  border-radius: 2px;
  padding: 0 2px;
  background-color: #3178c6;
  font-size: 0.7rem;
}

.todo-item.checked .todo-text {
  -webkit-filter: brightness(120);
          filter: brightness(120);
}

.todo-item.pending {
  opacity: 0.5;
  pointer-events: none;
}

.todo-item .todo-buttons button {
  font-size: 1.5rem;
  background: none;
  padding: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  transition: 100ms ease-in;
  width: 4rem;
}

.todo-item .todo-buttons button:hover,
.todo-item .todo-buttons button:active {
  background-color: rgba(100, 100, 100, 0.5);
}

